'use client';

import { motion } from 'framer-motion';

interface HoverAnimationProps {
  children: React.ReactNode;
  scale?: number;
  rotate?: number;
  duration?: number;
  className?: string;
}

const HoverAnimation = ({
  children,
  scale = 1.05,
  rotate = 0,
  duration = 0.3,
  className = '',
}: HoverAnimationProps) => {
  const hoverVariants = {
    rest: {
      scale: 1,
      rotate: 0,
    },
    hover: {
      scale,
      rotate,
      transition: {
        duration,
        type: "spring",
        stiffness: 400,
        damping: 10
      }
    }
  };

  return (
    <motion.div
      variants={hoverVariants}
      initial="rest"
      whileHover="hover"
      whileTap="hover"
      className={className}
    >
      {children}
    </motion.div>
  );
};

export default HoverAnimation;